<template>
	<view class="box">
		<view class="search-box">
			<uni-search-bar radius="100" placeholder="请输入店铺名" @confirm="reset" />
		</view>
		<mescroll-body ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback" :up="upOption">
		<template v-for="(item,k) in shop_list">
			<view @tap="jumpGood(k)" :key="k" :url="'good?merchantSeqId='+item.merchantSeqId+'&merchantShopId='+item.seqId" class="good-shop-box">
				<view class="good-shop">
					<view class="top">
						<view class="iconfont iconicon"></view>
						<view class="shop-name">
							{{item.merchantShopName}}
							<view class="id">
								{{item.shopAddress}}
							</view>
						</view>
					</view>
				</view>
			</view>
		</template>
		</mescroll-body>
	</view>
</template>

<script>
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue"	
	import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
	import Vue from 'vue'
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				shop_list:[],
				good_list:[],
				merchantSeqId:"",
				upOption: {
					textNoMore:"没有更多数据",
				},
				page:{
					num:1,
					size:100
				},
				shop_name:""
			}
		},
		components: {
			MescrollBody,
			uniSearchBar
		},
		
		methods: {
			reset(res){
				if(res){
					this.shop_name=res.value;
					this.page_index=1;
					this.shop_list=[];
					this.mescroll.resetUpScroll(true);
					this.mescroll.scrollTo(0,0)
				}
			},
			upCallback(page) {
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				this.$api.get('/o2oMerchantShop/findMyShopsByParam',{params:{
					userSeqId:this.$common.getLocalSync('customerSeqId'),
					merchantShopName:this.shop_name,
				}}).then((res) => {
					if (res.code && res.code != 200) {
						this.mescroll.endErr();
						return
					}
						// 接口返回的当前页数据列表 (数组)
						let curPageData = res.list;
						// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
						let curPageLen = curPageData.length;
						// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
						let totalPage = res.pages;
											
						//设置列表数据
						if (page.num == 1) this.shop_list = []; //如果是第一页需手动置空列表
						this.shop_list=this.shop_list.concat(curPageData)
						// 请求成功,隐藏加载状态
						//方法一(推荐): 后台接口有返回列表的总页数 totalPage
						this.mescroll.endByPage(curPageLen, totalPage);
				})
			},
			
			
			jumpGood(k){
				this.$store.commit('saveShop',this.shop_list[k]);
				uni.navigateTo({
					url:'good?merchantSeqId='+this.shop_list[k].merchantSeqId+'&merchantShopId='+this.shop_list[k].seqId
				})
			},
			loadShop(){
				this.$api.get('/o2oMerchantShop/findMyShop',{params:{
					userSeqId:this.$common.getLocalSync('customerSeqId')
				}}).then(res=>{
					if(res.code==200){
						this.shop_list=res.data
					}
				})
			}
			
		}
	}
</script>

<style lang="scss">
	.goods-box {
		background: white;
		.goods-li {
			width: calc(100% - 60rpx);
			padding: 40rpx 0;
			margin: 0 auto;
			height: auto;
			display: flex;
			justify-content: space-between;
			overflow: hidden;
			.img {
				width: 280rpx;
				height: 156rpx;
				flex-shrink: 0;
				margin-right: 20rpx;
				background: #e5e5e5;
			}
			.right {
				width: calc(100% - 300rpx);
				font-size: 30rpx;
				.title {
					width: 100%;
					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
				}
				.money-box {
					width: 100%;
					display: flex;
					justify-content: space-between;
					align-items: center;
					color: #666;
					font-size: 20rpx;
					.price {
						font-size: 30rpx;
						color: #f53a00;
					}
				}
				.yongjin-btn {
					width: 120rpx;
					height: 42rpx;
					line-height: 42rpx;
					border: 1px solid $color-primary;
					border-radius: 10rpx;
					text-align: center;
					color: $color-primary;
					margin: 10rpx 0;
				}
				.set-box {
					display: flex;
					.btn {
						margin-right: 20rpx;
					}
				}
			}
		}
	}
	
	.bg-red {
		background: #f6591e;
		color: white;
	}
	.bg-orange {
		background: #feba02;
		color: white;
	}
	
.good-shop {
	background: white;
	width: calc(100% - 60rpx);
	padding: 40rpx 30rpx;
	border-bottom: 1px solid #eee;
	.top {
		display: flex;
		align-items: center;
		.iconfont {
			width: 100rpx;
			height: 100rpx;
			border-radius:100%;
			background: $color-primary;
			color: white;
			text-align: center;
			line-height: 100rpx;
			font-size: 40rpx;
			margin-right: 26rpx;
		}
		.shop-name {
			font-size: 30rpx;
			.id {
				color: #93979f;
				font-size: 24rpx;
				margin-top: 10rpx;
			}
		}
	}
	.bottom {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-top: 40rpx;
		.money {
			font-size: 20rpx;
		}
		.right-bottom {
			width: 310rpx;
			flex-shrink: 0;
			display: flex;
			justify-content:space-between;
		}
		
	}
}
.btn {
			width: 140rpx;
			height: 60rpx;
			text-align: center;
			line-height: 60rpx;
			border-radius: 40rpx;
			font-size: 28rpx;
		}
		.bg-primary {
			background: $color-primary;
			color: white;
		}
		.border-primary {
			border:1px solid $color-primary;
			color: $color-primary;
		}
</style>
